import { createRouter, createWebHashHistory } from 'vue-router';
import Home from '../views/Home.vue';
import Directions from '../views/Directions.vue';
import ChoosenDirection from '../views/ChoosenDirection.vue';
import Emotions from '../views/Emotions.vue';

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/directions',
    name: 'Directions',
    component: Directions,
  },
  {
    path: '/choosen-direction',
    name: 'ChoosenDirection',
    component: ChoosenDirection,
  },
  {
    path: '/emotions',
    name: 'Emotions',
    component: Emotions,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
